import React, { useState } from 'react'
import './Dropdown.css'
import { MenuItems, ListItems, Lists } from './MenuItem'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import { useStateValue } from '../../ContextAPI/StateProvider'
import { auth } from '../../../firebase'

const Dropdown = () => {
  const [{ basket, user }, dispatch] = useStateValue()
  const [click, setClick] = useState(false)

  const handleClick = () => setClick(!click)

  const handleAuthentication = () => {
    if (user) {
      auth.signOut()
    }
  }

  return (
    <div>
      <div className='clip'></div>
      <ul
        onClick={handleClick}
        className={click ? 'dropdown-menu clicked' : 'dropdown-menu'}
      >
        <div className='items'>
          <div className='list'>
            <h4 className='title'>リスト</h4>
            {Lists.map((item, index) => {
              return (
                <li key={index}>
                  <Link
                    className={item.cName}
                    to={item.path}
                    onClick={() => setClick(false)}
                  >
                    {item.title}
                  </Link>
                </li>
              )
            })}
          </div>
          {ListItems.map((item, index) => {
            return (
              <li key={index}>
                <Link
                  className={item.cName}
                  to={item.path}
                  onClick={() => setClick(false)}
                >
                  {item.title}
                </Link>
              </li>
            )
          })}
        </div>
        <div className='items2'>
          <h4 className='title'>アカウントサービス</h4>
          {MenuItems.map((item, index) => {
            return (
              <li key={index}>
                <Link
                  className={item.cName}
                  to={item.path}
                  onClick={() => setClick(false)}
                >
                  {item.title}
                </Link>
              </li>
            )
          })}
          {user && (
            <>
              <li className='dropdown-link'>アカウントの切り替え</li>
              <Link to='/login' onClick={handleAuthentication}>
                <li className='dropdown-link'>ログアウト</li>
              </Link>
            </>
          )}
        </div>
      </ul>
    </div>
  )
}

export default Dropdown
