import React from 'react'
import './CheckoutProduct.css'
import { useStateValue } from '../../ContextAPI/StateProvider'
import { Link } from 'react-router-dom'
import CheckoutProductHeader from '../CheckoutProduct/CheckoutProductHeader'

const CheckoutProduct = ({ id, title, image, price, rating, hideButton }) => {
  const [{ basket }, dispatch] = useStateValue()
  const removeAllFromBasket = () => {
    //remove item from basket...
    dispatch({
      type: 'REMOVEALL_FROM_BASKET',
      id: id,
    })
  }
  const removeFromBasket = () => {
    //remove item from basket...
    dispatch({
      type: 'REMOVE_FROM_BASKET',
      id: id,
    })
  }
  return (
    <>
      <div className='checkoutProduct'>
        <img src={image} alt='' />
        <div className='checkoutProduct__info'>
          <h2 className='checkoutProduct__title'>{title}</h2>
          <p className='checkoutProduct__price'>
            <strong>¥ </strong>
            <strong>{price}</strong>
          </p>
          <div className='product__rating'>
            {Array(rating)
              .fill()
              .map((_, i) => (
                <p>⭐️</p>
              ))}
          </div>
          {!hideButton && <button onClick={removeFromBasket}>削除</button>}
        </div>
      </div>
      <hr />
      <div></div>
    </>
  )
}

export default CheckoutProduct
