export const initialState = {
  basket: [],
  user: null,
}

export const getBasketTotal = (basket) =>
  basket?.reduce((amount, item) => item.price + amount, 0)//0 la gia tri dau tien

const reducer = (state, action) => {
  console.log(action)
  switch (action.type) {
    case 'ADD_TO_BASKET':
      //logic for adding item to basket
      return {
        ...state,
        basket: [...state.basket, action.item],
      }

    case 'EMPTY_BASKET':
      return {
        ...state,
        basket: [],
      }

    case 'REMOVE_FROM_BASKET':
      //logic for removing item from basket

      //we cloned the basket
      let newBasket = [...state.basket]
      //wecheck to see if product exis ts,
      const index = state.basket.findIndex(
        (basketItem) => basketItem.id === action.id
      )

      if (index >= 0) {
        //item exists in basket, remove it

        newBasket.splice(index, 1)
        console.log(index)
      } else {
        console.warn(`Cant remove product (id: ${action.id}) as its not`)
      }
      return {
        ...state,
        basket: newBasket,
      }

    case 'REMOVEALL_FROM_BASKET':
      //logic for removing item from basket
      let allBasket = [...state.basket]
      const indexs = state.basket.findIndex(
        (basketItems) => basketItems.id === action.id
      )

      if (indexs >= 0) {
        //item exists in basket, remove it
        allBasket.splice(indexs)
      } else {
        console.warn(`Cant remove product (id: ${action.id}) as its not`)
      }
      return {
        ...state,
        basket: allBasket,
      }

    case 'SET_USER':
      return {
        ...state,
        user: action.user,
      }

    default:
      return state
  }
}

export default reducer
