import React, { useState } from 'react'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import MenuIcon from '@material-ui/icons/Menu'
import CloseIcon from '@material-ui/icons/Close'
import {
  SidebarData,
  DigitalContent,
  Catergories,
  Programs,
  setup,
} from './SidebarData'
import './Navbar.css'
import '../../../index.css'
import { useStateValue } from '../../ContextAPI/StateProvider'
import { auth } from '../../../firebase'
import NavigateNextIcon from '@material-ui/icons/NavigateNext'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'

const Navbar = () => {
  const [{ basket, user }, dispatch] = useStateValue()
  const [sidebar, setSidebar] = useState(false)
  const [drop, setDrop] = useState(false)

  const showSidebar = () => {
    setSidebar(!sidebar)
  }

  const showDrop = () => {
    setDrop(!drop)
  }
  const handleAuthentication = () => {
    if (user) {
      auth.signOut()
    }
  }

  return (
    <>
      <div className='navbar__Boss'>
        <div className='navbar__Left'>
          <div className='navbar'>
            <Link to='#' className='menu-bars'>
              <MenuIcon onClick={showSidebar} />
            </Link>
          </div>

          <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
            <h2>こんにちは、LEさん</h2>

            <ul className='nav-menu-items' onClick={showDrop}>
              <div className='class'>
                <h2>トレンド分析</h2>
                <p>
                  {SidebarData.map((item, index) => {
                    return (
                      <li key={index} className={item.cName}>
                        <Link to={item.path}>
                          <span>{item.title}</span>
                        </Link>
                      </li>
                    )
                  })}
                </p>
                <hr />
              </div>
              <div className='class'>
                <h2>デジタルコンテンツ＆デバイス</h2>
                <p>
                  {DigitalContent.map((item, index) => {
                    return (
                      <li key={index} className={item.cName}>
                        <Link to={item.path}>
                          <span>{item.title}</span>
                        </Link>
                      </li>
                    )
                  })}
                </p>
                <hr />
              </div>
              <div className='class'>
                <h2>カテゴリー</h2>
                <p>
                  {Catergories.map((item, index) => {
                    return (
                      <li key={index} className={item.cName}>
                        <Link to={item.path}>
                          <span>{item.title}</span>
                        </Link>
                      </li>
                    )
                  })}
                </p>
                <hr />
              </div>
              <div className='class'>
                <h2>プログラムと特典</h2>
                <p>
                  {Programs.map((item, index) => {
                    return (
                      <li key={index} className={item.cName}>
                        <Link to={item.path}>
                          <span>{item.title}</span>
                        </Link>
                      </li>
                    )
                  })}
                </p>
                <hr />
              </div>{' '}
              <div className='class'>
                <h2>ヘルプと設定</h2>
                <p>
                  {setup.map((item, index) => {
                    return (
                      <li key={index} className={item.cName}>
                        <Link to={item.path}>
                          {item.icon}
                          <span>{item.title}</span>
                        </Link>
                      </li>
                    )
                  })}
                  {user && (
                    <>
                      <p>
                        <Link to='/login' onClick={handleAuthentication}>
                          <li className='nav-text '>
                            <a>ログアウト</a>
                          </li>
                        </Link>
                      </p>
                    </>
                  )}
                  <br />
                  <br />
                  <hr />
                  <br />
                </p>
                <p></p>
              </div>
            </ul>
          </nav>
        </div>
        <div className='navbar__right'>
          <nav
            className={sidebar ? 'nav-menur active' : 'nav-menur'}
            onClick={sidebar && showSidebar}
          >
            <ul className=''>
              <li className='navbar-toggle'>
                <Link to='#'>
                  <CloseIcon onClick={showSidebar} fontSize='large' />
                </Link>
              </li>
              <div className='class'></div>
            </ul>
          </nav>
        </div>
      </div>
    </>
  )
}

export default Navbar
