import React from 'react'
import './Checkout.css'
import { useStateValue } from '../ContextAPI/StateProvider'
import { Link } from 'react-router-dom'
import CurrencyFormat from 'react-currency-format'
import CheckoutProduct from './CheckoutProduct/CheckoutProduct'
import CheckoutProductHeader from './CheckoutProduct/CheckoutProductHeader'
import Subtotal from './Subtotal'

const Checkout = ({}) => {
  const [{ basket, user }, dispatch] = useStateValue()

  //remove item from basket...

  return (
    <div className='checkout'>
      <div className='checkout__left'>
        <img
          className='checkout__ad'
          src='https://images-fe.ssl-images-amazon.com/images/G/09/kindle/journeys/YzVjY2UwM2Et/YzVjY2UwM2Et-MDZkOTlkZDMt-w3000._CB430990345_.jpg'
          alt=''
        />

        <h3>こんにちは、{user?.email}</h3>
        <div className='checkout__info'>
          {basket?.length === 0 ? (
            <div>
              <h2>お客様のAmazonカートに商品はありません。</h2>
              <p>
                以下の「あとで買う」に入っている商品、または
                <Link to='/'>お買い物を続ける</Link>
                を確認してください。
              </p>
            </div>
          ) : (
            <div>
              {/* List out all of the checkout products */}

              <CheckoutProductHeader />

              {basket.map((item) => (
                <CheckoutProduct
                  id={item.id}
                  title={item.title}
                  image={item.image}
                  price={item.price}
                  rating={item.rating}
                />
              ))}
            </div>
          )}
        </div>
      </div>
      <div className='checkout__right'>
        {basket.length > 0 && (
          <div>
            <Subtotal />
          </div>
        )}
      </div>
    </div>
  )
}

export default Checkout
